@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

.form {
    max-width: 500px;
    margin: 20px auto;
    padding: 50px 150px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #f4f9e9ea;
    box-shadow: 5px 20px 18px #424c44a7;
    $sans-serif: 'Inter', sans-serif;
    font-family: $sans-serif;
  }

  
label {
    display: block;
    margin-bottom: 10px;
    font-weight: bold;
}
  
input {
    width: clamp(120px, 50vw, 200px);
    height: 2.5rem;
    padding: 0 1.25rem;
    border-radius: 2px;
    margin: 0.625rem auto;
    transition: all 250ms;
    font-size: 1.1rem;
    @media (min-width: 768px) {
        width: clamp(10px, 35vw, 420px);
    }
    @media (max-width: 600px) {
        width: clamp(10px, 45vw, 150px);
    }

}
  
button {
    background-color: #284B63;
    color: #EEF0EB;
    padding: 10px;
    border: none;
    cursor: pointer;
    text-transform: uppercase;
}
  
button:hover {
    background-color: #0056b3;
}
  
input,
textarea {
  font-family: "Montserrat", sans-serif;
}

.close {
    position: absolute;
    right: 2px;
    top: 5px;
    cursor: pointer;
    z-index: 10;
    margin: 5px;
  }
  
.close:before,
.close:after {
    position: absolute;
    right: 20px;
    content: " ";
    height: 25px;
    width: 5px;
    background-color: #000000;
    margin: 5px;
}
  
.close:before {
    transform: rotate(45deg);
}
  
.close:after {
    transform: rotate(-45deg);
}

.form__name{
    font-size: 1.5rem;
    max-width: 25ch;
    margin: 0 auto;
    text-align: center;
    
    @media (min-width: 768px) {
    text-align: left;
    margin: 0;
    }
}


@media (max-width: 768px){
    .form {
        margin: 0px auto;
        padding: 25px 100px;

      }
}

@media (max-width: 600px){
    .form {
        margin: 0px auto;
        padding: 20px 80px;

      }
}